@import 'libs';
@import 'helpers/variables';
@import 'helpers/mixins';
@import 'general/fonts';
@import 'general/base';
@import '../blocks/blocks';

$dark-gray: #2b2b2b;
$orange-color: #f1ac11;

p,a,h1,h2,h3,h4,h5,h6,head,body,li{
	margin: 0;
	padding: 0;
	text-decoration: none;
	list-style-type: none;
}
body{
	font-family: 'Open Sans', serif;
	color: $dark-gray;
}
.counter-list{
	max-width: 720px;
	margin: -108px calc(55% - 34%) 0 calc(35% - -6%);
}
.nav{
	justify-content: space-between;
    display: flex;
    margin-top: 0px;
    
    max-width: 720px;
	&-item{
		font-size: 22px;
		font-style: italic;
		font-weight: 600;
		color: #fff;
		margin-right: 59px;
		padding: 0 0 12px;
		&:hover{
			color: $orange-color;
			border-bottom: 3px $orange-color solid; 
			transition: 0.2s;
		}
	}
}
.logo{
	background-image: url(../img/logo.png);
	width: 321px;
	height: 95px;
	margin-top: 54px;
    margin-left: 42px;
    display: block;
}
.header{
	padding-top: 1px;
	background-image: url(../img/header.png);
	background-size: 100%;
	background-repeat: no-repeat;
	padding-bottom: 405px;
	&-list{
		display: flex;
		justify-content: space-between;
		margin-top: 280px;
		&-item{
			background-image: url(../img/header-element-bg.png);
			height: 253px;
			width: 421px;
			&__one{
				margin-top: 58px;
    			margin-left: 115px;
				position: relative;
				margin-left: 46px;
    			margin-top: 39px;
    			&:hover{
					background-image: url(../img/header-element-bg-active.png);
					height: 253px;
					width: 421px;
    			}
    			&>p{
    				margin-top: 58px;
    				margin-left: 115px;
    				&>span{
    					margin-left: 24px;
    				}
    			}
    			&-btn{
    				position: absolute;
    				font-size: 11px;
				    padding: 9px 39px;
				    background-color: $orange-color;
				    border-radius: 3px;
				    top: 168px;
    				left: 140px;
    				color: $dark-gray;
				    font-weight: 700;
				    opacity: 0.7;
				    &:hover{
				    	opacity: 1;
				    }
    			}
			}
			&__two{
				position: relative;
				margin-left: 29px;
    			margin-top: -14px;
    			background-image: url(../img/header-element-bg-active.png);
				height: 253px;
				width: 421px;
				&>p{
					margin-left: 93px;
    				margin-top: 54px;
    				&>span{
    					margin-left: 37px;
    				}
				}
				&-btn{
					position: absolute;
					top: 166px;
    				left: 144px;
				    font-size: 11px;
				    padding: 9px 39px;
				    background-color: $orange-color;
				    border-radius: 3px;
				    color: $dark-gray;
				    font-weight: 700;
				}
			}
			&__three{
				position: relative;
				margin-right: 34px;
    			margin-top: 39px;
    			&:hover{
    				background-image: url(../img/header-element-bg-active.png);
    				height: 253px;
					width: 421px;
    			}
    			&>p{
    				margin-left: 131px;
    				margin-top: 58px;
    			}
    			&-btn{
    				position: absolute;
				    font-size: 11px;
				    padding: 9px 29px;
				    background-color: #f1ac11;
				    border-radius: 3px;
				    top: 166px;
				    left: 138px;
				    color: $dark-gray;
				    font-weight: 700;
				    opacity: 0.7;
				    &:hover{
				    	opacity: 1;
				    }
    			}
			}
			&>p{
				font-size: 38px;
				font-weight: 700;
				&>span{
					display: block;
				}
			}
		}
	}
}
.about-us{
	background-image: url(../img/about-us-bg.png);
	height: 100%;
    background-size: cover;
    padding-top: 1px;
	&-address{
		background-image: url(../img/address.png);
		margin: 34px auto;
		max-width: 647px;
		padding: 246px 0 164px 0;
		position: relative;
		&__title{
			position: absolute;
		    top: 118px;
		    left: 210px;
		    font-weight: 600;
		    font-style: italic;
		    font-size: 25px;
		    letter-spacing: 1.1px;

		}
		&__list{
			position: absolute;
			top: 189px;
			left: 200px;
			display: flex;
			justify-content: space-between;
			&-food{
				background-image: url(../img/food.png);
				display: block;
				height: 52px;
				width: 73px;
				position: absolute;
			    left: -88px;
			    top: -14px;
			}
			&>h3{
				color: $orange-color;
				font-weight: 600;
				letter-spacing: 1px;
    			margin-left: -2px;
			}
			&>p{
				line-height: 21px;
				margin-left: 68px;
    			margin-top: -10px;
			    letter-spacing: 0.5px;
				font-weight: 700;
				font-style: italic;
			}
		}
		&__btn{
			position: absolute;
    		top: 257px;
    		left: 269px;
		    font-style: italic;
		    font-size: 14px;
		    color: $dark-gray;
		    background-color: $orange-color;
		    padding: 9px 11px;
		    border-radius: 2px;
		}
	}
}
.footer{
	background-image: url(../img/footer-bg.png);
	color: #fff;
	font-style: italic;
	&-main{
		padding: 77px 0;
		max-width: 1170px;
		margin: 0 auto;
		display: flex;
		justify-content: space-between;
		&__item{
			&-one{
				display: flex;
				justify-content: space-around;
				&>div{
					margin-right: 54px;
				    margin-left: 26px;
				    letter-spacing: 0.5px;
				    line-height: 20px;
					&>h4{
					margin-bottom: 22px;
    				font-size: 20px;
    				font-weight: 400;
					}
				}
				&__list{
					color: #bbbbbb;
				}
			}
			&-two{
				margin-right: 167px;
    			font-size: 16px;
    			&>h3{
					font-weight: 400;
					letter-spacing: 1.6px;
					margin-bottom: 39px;
    			}
    			&>input{
    				padding: 5px 19px;
    				margin-right: 11px;
    				opacity: 0.5;
    				color: #fff;
    			}
    			&>button{
    				margin-right: -16px;
					padding: 9px 21px 9px 20px;
					font-size: 13px;
					font-style: italic;
					font-weight: 700;
					border-radius: 3px;
					border: none;
					background-color: $orange-color;
    			}
			}
		}
	}
}
.price{
